.learn-answer-enter {
    opacity: 0;
    transform: scale(0.9);
}

.learn-answer-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 0ms, transform 0ms;
}

.learn-answer-exit {
  opacity: 1;
}

.learn-answer-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 0ms, transform 0ms;
}
  